<template>
    <div>
         <ol class="list-group list-group">
            <li v-for="fruta, index of arrayOrdenado" :key="fruta.id" @click="aumentar(index)"
            class="list-group-item d-flex justify-content-between align-items-start">
              <div class="ms-2 me-auto">
                <div class="fw-bold">  {{index}} {{fruta.nombre}}</div>
                  
                </div>
                <span class="badge bg-primary rounded-pill">{{fruta.cantidad}}</span>
            </li>
            <button class="btn btn-danger btn-block" @click="restablecer">Restablecer</button>
        </ol>
    </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
    export default {
        name:'Lista',
        computed:{
            ...mapState(['frutas']),
            arrayOrdenado(state){
                var orden = (a, b) => b.cantidad - a.cantidad;
                return state.frutas.sort(orden);
            } 
        },
        methods:{
            ...mapMutations(['aumentar', 'restablecer']),
        }
    }
</script>

