import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
    state:{
        frutas:[
         {nombre:'Manzana', cantidad: 0},
         {nombre:'Pera', cantidad: 0},   
         {nombre:'Sandias', cantidad: 0}
        ]
    },
    mutations:{
        aumentar(state, id){
            state.frutas[id].cantidad++
        },
        restablecer(state){
            for(let frutas of state.frutas){
                frutas.cantidad = 0
            }
        }
    }
})